.stars {
  display: flex;
  align-items: center;

  &__star {
    width: 16px;
    height: 16px;
    margin-right: 4px;

    background-image: url('../images/star.svg');
    background-repeat: no-repeat;
    background-position: center;

    &--active {
      background-image: url('../images/star-active.svg');
    }
  }
}
